<template>
  <div class="play">
    <div class="column" v-if="question">
      <div class="box">
        <div class="line upper">
          <div class="question">
            <p>Question:</p>
            <div>{{ question }}</div>
          </div>
          <div class="hitRate">
            <div class="rate">{{ getHitRate }}%</div>
            <div class="tries">
              {{ getTries }}
            </div>
          </div>
        </div>
        <div class="line downer">
          <div class="history" v-if="history.length">
            <div class="line dots">
              <div v-for="(el, index) in history" :key="index">
                <div v-if="el" class="dot green-dot"></div>
                <div v-else class="dot red-dot"></div>
              </div>
            </div>
            <p>Last 10 tries</p>
          </div>
          <div v-else class="history no-history">No history</div>
          <button id="next" class="primary" @click="next">Next</button>
        </div>
      </div>
      <div class="answers">
        <div
          v-for="(el, index) in answers"
          :key="index"
          class="answer"
          :class="el['class']"
          @click="checkAnswer(index, id)"
        >
          {{ el["answer"] }}
        </div>
      </div>
    </div>
    <div v-else class="column">
      <p>No questions<br />Please add a question</p>
    </div>
  </div>
</template>

<script>
import store from "../store";

function print(...valu) {
  console.log(valu);
}

function shuffle(array) {
  console.log("Shuffle answers");
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
}

function getRand(max_value) {
  return Math.floor(Math.random() * max_value);
}

export default {
  data() {
    return {
      // question: "In publishing and graphic design, Lorem ipsum is a placeholder text commonly used to demonstrate the visual form of a document or a typeface without relying on meaningful content.",
      // question: "",
      id: "isus",
      question: "I just got into the car, alahu akbar",
      answers: [
        { answer: "yes", class: "" },
        { answer: "perhaps", class: "" },
        { answer: "OGK", class: "" },
        {
          answer:
            "Vaya con Dios In publishing and graphic design, Lorem ipsum is a placeholder text commonly used to demonstrate the visual",
          class: "",
        },
      ],
      history: [1, 1, 0],
      goodAnswer: "yes",
      answerClicked: false,
      hit: 0,
      miss: 0,
      auto_jump: JSON.parse(localStorage.getItem("auto_jump_to_next")),
    };
  },
  methods: {
    checkAnswer(index, id) {
      print("id:", id);
      if (this.answerClicked) {
        // do not check answer if it has already been clicked
        return;
      }
      for (var el in this.answers) {
        var answer = this.answers[el];
        if (answer["answer"] == this.goodAnswer) {
          answer["class"] = "good-answer";
          if (el == index) {
            this.addToHistory(1);
            answer["class"] = "good-answer-emphasize";
            store.commit("incrementHit", this.id);
          }
        } else {
          if (el != index) {
            answer["class"] = "bad-answer";
          } else {
            this.addToHistory(0);
            answer["class"] = "bad-answer-emphasize";
            store.commit("incrementMiss", this.id);
          }
        }
      }
      if (this.auto_jump){
        setTimeout(()=>this.next(),1500)
      }
      this.answerClicked = true;
    },
    addToHistory(value) {
      var len = this.history.length;
      if (len < 10) {
        this.history = [...this.history, value];
      } else {
        this.history.shift();
        this.history = [...this.history, value];
      }
      if (value) {
        this.hit += 1;
      } else {
        this.miss += 1;
      }
    },
    next() {
      print("next");
      for (var el in this.answers) {
        this.answers[el]["class"] = "";
      }
      this.answerClicked = false;

      var dummy_bad_answers = [
        "aferat",
        "casuna",
        "idiosincrasie",
        "perimat",
        "BMW",
        "adventisti",
        "Iulius Caesar",
        "naturale",
        "blonde",
        "arrow",
        "turkey",
        "conception",
        "seat",
        "hunter",
      ];
      var questions = store.state.allQuestion;
      var rand = getRand(questions.length);
      var chosen = questions[rand];
      var badAns = [];
      print("before SET:", badAns);
      this.id = chosen["id"];
      this.question = chosen["question"];
      this.goodAnswer = chosen["answer"][getRand(chosen["answer"].length)];
      this.answers[0]["answer"] = this.goodAnswer;
      this.history = chosen["history"];
      this.hit = chosen["hit"];
      this.miss = chosen["miss"];
      var first_2_char = this.goodAnswer.substring(0, 2).toLowerCase();
      var set_answer = new Set(this.goodAnswer);

      function set_has_subset(subset, set) {
        for (var el of subset) {
          if (!set.has(el)) {
            return false;
          }
        }
        return true;
      }

      for (var el in store.state.allBadAnswers) {
        // first, get all bad answers that starts with first 2 characters of the good Answer
        var bad = store.state.allBadAnswers;
        if (bad[el].toLowerCase().startsWith(first_2_char, 0)) {
          print("detected same 2 characters");
          if (!badAns.includes(bad[el]) && bad[el] != this.goodAnswer) {
            badAns.push(bad[el]);
            print("push element because has same 2 first letters:", bad[el]);
          }
        }

        // we make 2 sets, set_answer & set_bad. If the difference between them is less than 30%( length aka size )
        // we check if the smaller set is a subset of the greater one
        var set_bad = new Set(bad[el].toLowerCase());
        var delta = 0;
        if (set_bad.size > set_answer.size) {
          delta = set_bad.size / set_answer.size;
        } else {
          delta = set_answer.size / set_bad.size;
        }
        // print("delta", delta);
        var canAppendSet = false;
        if (delta < 1.3) {
          if (set_bad.size > set_answer.size) {
            canAppendSet = set_has_subset(set_answer, set_bad);
          } else {
            canAppendSet = set_has_subset(set_bad, set_answer);
          }
        }
        if (canAppendSet) {
          if (!badAns.includes(bad[el]) && this.goodAnswer != bad[el]) {
            badAns.push(bad[el]);
            print("push SET:", bad[el]);
          }
        }
      }

      // apend random element if bad answer list is less than 3 element
      if (badAns.length < 3) {
        while (badAns.length < 5) {
          var rand_elem = getRand(dummy_bad_answers.length);
          rand_elem = dummy_bad_answers[rand_elem];
          if (!badAns.includes(rand_elem) && rand_elem != this.goodAnswer) {
            print("badAns < 3 , push element", rand_elem);
            badAns.push(rand_elem);
          }
          // print("random chosen element", rand_elem);
        }
      }

      var final_answers = [this.goodAnswer, ...chosen["badAnswers"]];
      while (final_answers.length < 4) {
        var pick_rand = getRand(badAns.length);
        pick_rand = badAns[pick_rand];
        if (!final_answers.includes(pick_rand)) {
          final_answers.push(pick_rand);
        }
      }

      // print("ob", ob);
      print("badAns", badAns);
      print("all bad store", store.state.allBadAnswers);
      shuffle(final_answers);
      print("final answer", final_answers);
      for (var el in [0, 1, 2, 3]) {
        this.answers[el]["answer"] = final_answers[el];
      }
    },
  },
  created() {
    console.log("on created shuffle answers");
    if (store.state.allQuestion.length) {
      this.next();
    } else {
      this.question = "";
    }
    shuffle(this.answers);
  },
  computed: {
    getTries() {
      var tries = this.miss + this.hit;
      if (tries == 1) {
        var str = tries + " try";
        return str;
      } else {
        var str = tries + " tries";
        return str;
      }
    },
    getHitRate() {
      var tries = this.hit + this.miss;
      if (tries == 0) {
        return 0;
      } else {
        return Math.floor((this.hit / tries) * 100);
      }
    },
  },
};
</script>

